@import url(https://fonts.googleapis.com/css?family=Montserrat:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.material-button {
    width: 140px;
    height: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 25px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    margin: 8px;
    z-index: 4;
    position: relative;
    -webkit-tap-highlight-color: transparent; /* for remove highlight */
}

.material-button:hover {
    background-color: #f20700;
    box-shadow: 0px 15px 20px rgba(229, 10, 0, 0.4);
    color: #fff;
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
}
.App {

}
.home {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.home-logo-container {
    width: 100%;
    display: grid;
    align-content: center;
}

.home-logo-container img {
    width: 125%;
    margin-top: -100px;
}

.home-background-container div {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    width: 100%;
    height: 100%;
    align-items: center;
    overflow: hidden;
    margin-top: 38vh;
    margin-left: 5vw;
}

.home-background-container img {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    width: 100%;
    height: 100%;
    object-fit: fill;
}

.home-background-container {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    overflow: hidden;
}

.home-buttons-container {
    z-index: 3;
    display: flex;
    flex-direction: column;
}

@media only screen and (min-width: 789px) {
    .home-logo-container img {
        width: 70%;
        margin-top: -100px;
    }

    .home-background-container img {
        grid-column: 1 / -1;
        grid-row: 1 / -1;
        width: 50%;
        height: 100%;
        object-fit: fill;
        justify-self: right;
    }

    .home-background-container div {
        grid-column: 1 / -1;
        grid-row: 1 / -1;
        width: 100%;
        height: 100%;
        align-items: center;
        overflow: hidden;
        margin-top: 38vh;
        margin-left: 15vw;
    }
}

.NavBar {
    width: 100vw;
    height: 100px;
    position: absolute;
}

.list-style .right-side {
    visibility: hidden;
}

.list-style {
    padding-left: 40px;
    padding-top: 20px;
}

.list-style li {
    display: inline;
    font-size: 25px;
    margin: 10px;
    color: red;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
}

@media only screen and (min-width: 789px) {
    .list-style .right-side {
        visibility: visible;
        position: absolute;
        right: 0;
        padding: 0;
        margin: 0 10px 0 0;
    }
}
